*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}


body{
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.5;
  background-color: black;
}

body::-webkit-scrollbar{
  
 width: 1px;
}



p{
  color: white;
}

/* Card  */
div.card{
  width: 12rem;
  cursor: pointer;
  
} 

div.card div#img{
 
 
  width: 8rem;
  height: 12rem;
  background-color: #ccc;
  border-radius: 1rem;
}

div.card img{
  border-radius: 1rem;
  width: 8rem;
  height: 12rem;
  object-fit: fill;
}

div#content
{
  display: grid;
  align-items: center;
  justify-content: center;
  width: 8rem;
  place-items: center;
 

}
div#content p
{
 color: white;
 font-size: .8rem;
 padding: .3rem;


 
}



/* Pop movies*/

div#pop-movies{

  display: flex;
  column-gap: .5rem;
  overflow: auto;
  justify-content: center;
  justify-items: center;
  align-items: center;
  justify-items: center;
  margin: auto;
  width: 90%;
  

}

div#pop-movies::-webkit-scrollbar{
  width: 0;
}

h3#main-header
{
  color:white;
  padding-left: 4rem;
 padding-top: .5rem;
}


/*Search */

form#toggle input#toggle{
 width: 100%;
  height: 2rem;
  border-radius: 1rem;
  border: none;
  margin-left: 3.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  
  
} 

form#toggle{

  padding-top: 1rem;
  width: 100%;
  

  
  
}



@media (max-width:280px) {
  
  form#toggle input#toggle{

    width: 100%;
    margin: auto;
  }
}


@media (max-width:850px) {
  h3#main-header{
    padding-left: 2rem;
  }
  
}
@media (max-width:550px)
{
  form{display: grid;
      place-items: center;
      padding-top: 1rem;
 
  }
  
 form#toggle input#toggle{
    
    width: 100%;
    display: grid;
    margin: auto;

   
     
    
    

  }

  form#toggle{
    display: grid;
     place-items: center;
     place-content: center;
     
     
  }

  

 
 
  a{
      width: 90%;
      display: grid;
      place-items: center;
      
  }
} 

@media (max-width:730px) {

  form#toggle{
 
    display: grid;
   

  }
  form#toggle ul{

   display: none;

  }
  
}

section
{
  padding: 2rem;
  background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSCtOLgXSLl5gsrm770Ptu3aidImdX6elrXheA50VKs5g&s");
  background-position: center;
  background-size: cover;
  
}

section#movie main{

   display: flex;
   place-items: center;
   grid-gap: 1rem;
   place-content: center;
}

section#movie main div#content{
  width: 20rem;
  display: grid;
  place-content: start;
  place-items: start;
} 
main div#content  span{
   color: white;
   font-weight: 100;
}

main div#content h5{
  color: white;

} 

section#movie div#img{
  width: 18rem;
  height: 12rem;
  object-fit: fill;
}
section#movie img{

  width: 18rem;
  border-radius: 1rem;

  height: 12rem;
  object-fit: cover;
}


section#movie aside#aside{
  display: flex;
   gap: .5rem;
  
  
}
section#movie aside#aside button{
  cursor: pointer;
  
  
}
section#movie aside#aside button.btn{

  background: red;
  border: none;
}

@media(max-width:655px) {
  
  section#movie{
    display: none;
    
  }
}
form{
  display: flex;
  justify-content: space-between;
}

form ul{
  display: flex;
  column-gap: .5rem;
  padding-right: 5rem;
} 

ul a{
   color: white;
   background: none;
  
}
 

div#seachMovie{
 
  display: grid;
  overflow-x: hidden;
  padding: 2rem;
  gap: .5rem;
  grid-template-columns: repeat( auto-fit, minmax(120px, 1fr));
  

} 

img#backimg{

width: 100%;
height: 400px;
object-fit: cover;
position: relative;
filter: brightness(.8);
}

div#movie-content p{
  padding-top: .5rem;
}
div#movie-content{
 position: absolute;
 display: flex;
 column-gap: 1rem;
 place-items: center;
 place-content: center;

  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  height: 200px;
  top: 200px;
  bottom: 0;
  left: 0;
  
}

img#profile{
  border-radius: 50%;
  width: 35px;
  height: 35px;
  object-fit: cover;
  cursor: pointer;
  margin-top: -.5rem;
}
div#overview{
  width: 50%;

}

img#frontimg{
  width: 200px;
  height: 150px;
  object-fit: cover;
  border-radius: 1rem;
}
iframe{
  border: none;
  object-fit: cover;
  filter: brightness(.9)
  
}


nav#header{
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  border-bottom: 1px solid #ccc;
}

nav#header ul{

display: flex;
grid-gap: 1rem;
}

li{
  list-style: none;

}


@media (max-width:800px) {

  div#movie-content{
    display: grid;


  } 
 div#overview p{
  padding-top: 0;
 }
  div#overview{

    width: 100%;
    padding:  .5rem;
    
   
  }
  img#frontimg{
    display: none;
  }
  
}



/*sign in*/
label {
 
  font-size: 11pt;
}
#forgot-pass {
  color: black;
 
  font-size: 10pt;
  margin-top: 3px;
  text-align: right;
}
#card {
  background: #fbfbfb;
  border-radius: 8px;
  box-shadow: 1px 2px 8px rgba(0, 0, 0, 0.65);
  height: 410px;
  margin: auto;
  
  width: 329px;
}
#card-content {
  padding: 12px 44px;
  
}
#card-title {
  

  padding-bottom: 23px;
  padding-top: 13px;
  text-align: center;
}
#signup {
  color: red;
 
  font-size: 10pt;
  margin-top: 16px;
  text-align: center;
}
#submit-btn {
  background: -webkit-linear-gradient(right, rgb(237, 50, 50), red);
  border: none;
  border-radius: 21px;
  box-shadow: 0px 1px 8px rgb(234, 53, 53);
  cursor: pointer;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  height: 2rem;
  margin: 0 auto;
  margin-top: 50px;
  transition: 0.25s;
  width: 150px;
}
#submit-btn:hover {
  box-shadow: 0px 1px 18px rgb(251, 68, 68);
}
.form {
  align-items: left;
  display: flex;
  flex-direction: column;
}
.form-border {
  background: -webkit-linear-gradient(right, rgb(237, 50, 50), red);
  height: 1px;
  width: 100%;
}
.form-content {
  background: #fbfbfb;
  border: none;
  outline: none;
  padding-top: 14px;
}
.underline-title {
  background: -webkit-linear-gradient(right,rgb(237, 50, 50), red);
  height: 2px;
  margin: -1.1rem auto 0 auto;
  width: 89px;
}

a{
  text-decoration: none;
  color: black;
}

div#wrapper
{
  height: 100vh;
  display: grid;
  width: 100%;
  place-items: center;
  place-content: center;
  background-image: url('https://i.stack.imgur.com/a1bUS.png') ;
  background-size: cover;
  background-position: center;


}

div#wrap{

  display: flex;
  place-content: center;
 
  place-items: center;
  column-gap: 1rem;
  margin-top: 2rem;


}

div#cardNew{

  background: white;
  padding: 1rem;
  width: 20rem;
  font-family: Arial, Helvetica, sans-serif;
  display: grid;
  place-items: center;
  place-content: center;
  border-radius: 8px;
  box-shadow: 1px 2px 8px rgba(0, 0, 0, 0.65);
  
}



div#cardNew img{
  width: 100%;
}
div#cardNew a {
  
  padding-left: .5rem;
  font-size: 12px;
}

@media (max-width:800px) {

  div#wrap{
    display: grid;
    text-align: center;
    line-height: 1.5;
    margin-top: 0;
  }
  div#cardNew{

    display: grid;
    place-content: center;
    place-content: center;
  }
  
}